import { EventEmitter } from './events';
import { IWatcher, allWatchers } from './watcher';

abstract class AbstractRecorder extends EventEmitter {
  watchers: IWatcher[] = []
  start() { }
  stop() { }
}

type RecorderOption = {
  disableWatchers: string[]
};

export class Recorder extends AbstractRecorder {
  constructor({ disableWatchers }: RecorderOption) {
    super();
    this.watchers = allWatchers.filter((x: IWatcher) => disableWatchers.indexOf(x.name) === -1);
  }
}